.account-replenishment {
  margin-top: 50px;
  margin-bottom: 80px;
}

.account-replenishment__title {
  font-size: 30px;
}

.account__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 40px;
}

.account__list-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 23%;
  margin-bottom: 30px;
  padding: 40px;

  text-align: center;

  background-color: $basic-grey;
  border-radius: 20px;

  &:nth-child(1) {
    background: linear-gradient(180deg, #ffba77 0%, #ffaf36 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(2) {
    background: linear-gradient(180deg, #fba3a0 0%, #fc5b89 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(3) {
    background: linear-gradient(180deg, #7695ff 0%, #8342f7 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(6) {
    background: linear-gradient(180deg, #7695ff 0%, #8342f7 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(5) {
    background: linear-gradient(180deg, #ffba77 0%, #ffaf36 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(4) {
    background: linear-gradient(180deg, #5c84ff 0%, #1551fd 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }
}

.account__list-balance {
  margin-bottom: 40px;

  font-size: 24px;
  font-family: "Oswald", "Arial", sans-serif;
  color: $basic-white;


  & span {
    font-weight: 600;
    font-size: 20px;
  }
}

.account__list-balance--grey {
  color: #ffba77;
}

.account__list-link {
  padding: 5px 25px;
  padding-left: 30px;

  color: $basic-white;
  text-decoration: none;

  background-image: url("../img/plus.svg");
  background-repeat: no-repeat;
  background-position: 30px center;
  background-size: 12px;
  border: 1px solid $basic-white;
  border-radius: 20px;

  &:hover {
    color: $basic-white;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

/* Firefox */
input[type="number"] {
  appearance: textfield;
}

@media screen and (max-width: 991px) {
  .account__list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 40px;
  }

  .account__list-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40%;
    margin-bottom: 30px;
    padding: 40px;
  }
}

@media screen and (max-width: 767px) {
  .account__list {
    flex-direction: column;
    align-items: center;
  }

  .account__list-item {
    width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .account__list-item {
    width: 80%;
  }

  .account__list-link {
    background-position: 80px center;
  }
}

@media screen and (max-width: 480px) {
  .account__list-link {
    background-position: 50px center;
  }
}

@media screen and (max-width: 390px) {
  .account__list-link {
    background-position: 30px center;
  }
}
