.easyoffice-sections {
  margin-top: 50px;
}

.easyoffice-sections__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-row-gap: 30px;
}

.easyoffice-sections__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;

  font-weight: 700;
  font-size: 17px;
  line-height: 22px;
  text-align: center;
  color: $basic-white;
  text-transform: uppercase;

  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2);

  &:nth-child(1) {
    background: linear-gradient(180deg, #ffba77 0%, #ffaf36 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(2) {
    background: linear-gradient(180deg, #fba3a0 0%, #fc5b89 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(3) {
    background: linear-gradient(180deg, #7695ff 0%, #8342f7 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(4) {
    background: linear-gradient(180deg, #ffba77 0%, #ffaf36 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(5) {
    background: linear-gradient(180deg, #fba3a0 0%, #fc5b89 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }

  &:nth-child(6) {
    background: linear-gradient(180deg, #7695ff 0%, #8342f7 100%),
      linear-gradient(0deg, #ffffff, #ffffff);
  }
}

.easyoffice-sections__header {
  padding-bottom: 20px;

  font-weight: 600;
  font-size: 18px;
  color: $basic-white;

  border-bottom: 1px solid $special-light-grey;
}

.download-link {
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  padding-top: 20px;
  padding-left: 35px;

  font-weight: 400;
  font-size: 14px;
  font-family: "Calibri", sans-serif;
  color: $basic-white;
  text-transform: none;
  text-decoration: none;

  background-image: url("../img/excel.svg");
  background-repeat: no-repeat;
  background-position: left 15px;
  background-size: 30px;
  border-top: 1px solid $special-light-grey;

  &:hover {
    color: green;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.easyoffice-sections__balance {
  margin-top: 20px;

  font-weight: 400;
  font-size: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: $basic-white;
  text-transform: none;

  grid-column-gap: 20px;
  grid-template-columns: 1fr;

  & span {
    font-family: "Calibri", "Arial", sans-serif;
    font-weight: 600;
  }
}

@media screen and (max-width: 991px) {
  .easyoffice-sections__list {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 550px) {
  .easyoffice-sections__list {
    grid-template-columns: 1fr;
  }
}
