body {

}

* {
  box-sizing: border-box;
}

#notfound {
  position: relative;

  height: 100vh;
}

#notfound .notfound {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

.notfound {
  width: 100%;
  max-width: 767px;
  padding: 0 15px;

  line-height: 1.4;
}

.notfound .notfound-404 {
  position: relative;

  height: 150px;
  margin-bottom: 25px;

  line-height: 150px;
}

.notfound .notfound-404 h1 {
  margin: 0;

  font-weight: 900;
  font-size: 186px;
  font-family: "Calibri", sans-serif;
  text-transform: uppercase;

  background: url("../img/text.png");
  background-position: center;
  background-size: cover;
  background-clip: text;

  text-fill-color: transparent;
}

.notfound h2 {
  margin: 0;

  font-weight: 700;
  font-size: 26px;
  font-family: "Calibri", sans-serif;
}

.notfound p {
  margin-bottom: 0;

  font-weight: 500;
  font-size: 16px;
  font-family: "Calibri", sans-serif;
}

.notfound a {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 40px;

  font-weight: 700;
  font-size: 14px;
  font-family: "Calibri", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;

  background: $basic-yellow;
  border: none;
  border-radius: 20px;

  transition: 0.2s all;
}

.notfound a:hover {
  opacity: 0.8;
}

@media only screen and (max-width: 767px) {
  .notfound .notfound-404 {
    height: 110px;

    line-height: 110px;
  }

  .notfound .notfound-404 h1 {
    font-size: 120px;
  }
}


img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.page {
  height: 100%;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;

  font-family: "Calibri", "Arial", sans-serif;
  color: $basic-black;
}

.content {
  flex: 1 0 auto;
}
