.personal-info--edit {
  display: grid;
  grid-template-columns: 350px 1fr;
}

.edit-profile {
  margin-top: 50px;
  padding: 30px 50px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}

.edit-profile__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 100px;
  grid-row-gap: 30px;
  grid-template-columns: 1fr 1fr;
}

.edit-profile__item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.edit-profile__header {
  margin-right: 30px;

  font-weight: 600;
}

.edit-profile__text {
  margin-bottom: 3px;
  text-align: right;
}

.personal-info__photo-subtitle {
  font-size: 13px;
  color: $special-black;
}

.personal-info__photo-photo-subtitle--error {
  font-size: 13px;
  color: $basic-red;
}

.edit-profile__item--wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.edit-profile__link {
  color: $basic-yellow;
  text-align: right;

  &:hover {
    color: $basic-yellow;

    opacity: 0.7;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

@media screen and (max-width: 1200px) {
  .edit-profile__list {
    grid-column-gap: 80px;
  }
}

@media screen and (max-width: 991px) {
  .personal-info--edit {
    grid-template-columns: 1fr;
  }

  .edit-profile__list {
    padding: 0;
  }
}

@media screen and (max-width: 767px) {
  .personal-info--edit {
    grid-template-columns: 1fr;
  }

  .edit-profile {
    margin-top: 30px;
  }

  .edit-profile__list {
    grid-template-columns: 1fr;
    padding: 0;
  }
}
