.easyoffice-graphic--wrapper {
  display: grid;
  margin-top: 80px;
  margin-bottom: 80px;

  grid-row-gap: 80px;
  grid-template-columns: 1fr;
}

.carousel {
  margin-top: 30px;
}

.carousel-item {
  display: block;
  margin: 0 auto;
}

.graphic-list__header {
  font-size: 18px;
  margin-bottom: 50px;
  text-align: center;
  text-transform: uppercase;
}

.graphic-list__data {
  @include reset-list;
}

.graphic-list__data-item {
  font-size: 18px;
  margin-bottom: 10px;
  padding-left: 30px;
  font-family: "Oswald", "Arial", sans-serif;

  & span {
    font-family: "Calibri", "Arial", sans-serif;
  }
}

.prev-icon--office {
  top: 40%;
}

.next-icon--office {
  top: 40%;
}

.graphic-list {
  display: block;
  margin: 0 auto;
}


.graphic-list__data-item--usd {
  position: relative;

  &::before {
    position: absolute;
    top: 2px;
    left: 0;
    content: "";
    width: 20px;
    height: 20px;
    background-color: #ffaf36;
    border-radius: 100%;
  }
}

