canvas{
			-moz-user-select: none;
			-webkit-user-select: none;
			-ms-user-select: none;
		}
		.chartjs-tooltip {
			opacity: 1;
			position: absolute;
			background: rgba(0, 0, 0, .7);
			color: white;
			border-radius: 3px;
			-webkit-transition: all .1s ease;
			transition: all .1s ease;
			pointer-events: none;
			-webkit-transform: translate(-50%, 0);
			transform: translate(-50%, 0);
			padding: 4px;
		}

		.chartjs-tooltip-key {
			display: inline-block;
			width: 10px;
			height: 10px;
		}

@media screen and (max-width: 991px) {
  .easyoffice-graphic--wrapper {
    display: flex;
    flex-direction: column;
  }

  .chat--easyoffice {
    order: 2;
    margin-top: 40px;
  }

  .prev-icon--office {
    top: 40%;
  }

  .next-icon--office {
    top: 40%;
  }

  .graphic-list {
    margin-top: 10px;
  }
}
